<template>
  <el-drawer
    :model-value="visible"
    title="高级筛选"
    size="550"
    class="custom-drawer"
    :before-close="handleClose"
  >
    <el-form label-position="top" :model="formData">
      <el-form-item label="企业名称">
        <el-input
          v-model="formData.companyName"
          placeholder="请输入"
          clearable
        />
      </el-form-item>
      <el-form-item label="订单编号">
        <el-input v-model="formData.orderNo" placeholder="请输入" clearable />
      </el-form-item>
      <el-form-item label="产品名称">
        <el-input
          v-model="formData.combineServiceName"
          placeholder="请输入"
          clearable
        />
      </el-form-item>
      <el-form-item label="产品线筛选">
        <el-select
          v-model="formData.orderCategory"
          placeholder="全部"
          clearable
        >
          <el-option
            v-for="item in productTypeList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="下单时间">
        <el-date-picker
          v-model="initData.dateList"
          @change="changeDate"
          type="daterange"
          value-format="YYYY-MM-DD"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
        />
      </el-form-item>
      <el-form-item label="订单状态">
        <el-select v-model="formData.payStatus" placeholder="全部" clearable>
          <el-option
            v-for="(item, index) in customerOrderStatusList"
            :key="index"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="订单类型">
        <el-select v-model="formData.orderWay" placeholder="全部" clearable>
          <el-option
            v-for="(item, index) in customerOrderTypeList"
            :key="index"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="handleClose">关闭</el-button>
      <el-button type="primary" @click="handleSearch">搜索</el-button>
    </template>
  </el-drawer>
</template>
<script lang="ts" setup>
import { shallowReactive } from "vue";

import {
  productTypeList,
  customerOrderTypeList,
  customerOrderStatusList,
} from "@assets/typeStatus";
import { useModel } from "@assets/useHook";

const props = defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
  (e: "emitFunc", value: Record<string, any>);
}>();
const visibleModel = useModel(props, "visible", emits);
const initData = shallowReactive({
  dateList: [],
});
const formData = shallowReactive({
  companyName: "",
  orderNo: "",
  combineServiceName: "",
  orderCategory: "",
  startCreatedTime: "",
  endCreatedTime: "",
  payStatus: "",
  orderWay: "",
});

const handleSearch = () => {
  emits("emitFunc", formData);
  handleClose();
};
// 关闭弹框
const handleClose = () => {
  visibleModel.value = false;
};
// 选择日期
const changeDate = (value) => {
  if (value) {
    formData.startCreatedTime = value[0];
    formData.endCreatedTime = value[1];
  } else {
    formData.startCreatedTime = "";
    formData.endCreatedTime = "";
    initData.dateList = [];
  }
};
</script>
<style lang="scss" scoped>
:deep(.el-select) {
  width: 100%;
}
</style>
